@font-face {
    font-family: 'Oswald';
    src: url('../fonts/oswald/Oswald-LightItalic.woff2') format('woff2'), /* Super Modern Browsers */
         url('../fonts/oswald/Oswald-LightItalic.woff') format('woff'), /* Pretty Modern Browsers */
         url('../fonts/oswald/Oswald-LightItalic.ttf')  format('truetype'); /* Safari, Android, iOS */
    font-weight: 300;
    font-style: normal;
    font-display: swap;
}

